import { Footer } from "./common/footer/Footer";
import { Header } from "./common/header/Header";
import { Main } from "./pages/main/Main";
import { LoginPage } from "./pages/LoginPage/LoginPage";
import { Goods } from "./pages/Goods/Goods";
import { Category } from "./pages/Category/Category";
import { Basket } from "./pages/Basket/Basket";
import { NotFoundPage } from "./pages/NotFoundPage/NotFoundPage";
import { Routes, Route } from "react-router";
import { AboutGood } from "./pages/AboutGood/AboutGood";
import { useState } from "react";
import { ToastContainer } from "react-toastify";
import "react-toastify/dist/ReactToastify.css";

function App() {
  const [cartItem, setCartItem] = useState([]);

  const addToCart = (good) => {
    const goodExist = cartItem.find((item) => item.id === good.id);

    if (goodExist) {
      setCartItem(
        cartItem.map((item) =>
          item.id === good.id ? { ...goodExist, gty: goodExist.gty + 1 } : item
        )
      );
    }
  };

  return (
    <>
      <ToastContainer />
      <Header />
      <Routes>
        <Route path="/" element={<Main />} />
        <Route path="/login" element={<LoginPage />} />
        <Route
          path="/category/:catId"
          element={<Goods addToCart={addToCart} />}
        />
        <Route path="/catalog" element={<Category categoryTitle />} />
        <Route path="/basket" element={<Basket />} />
        <Route
          path="/category/:catId/good/:goodId"
          element={<AboutGood addToCart={addToCart} />}
        />
        {/* <Route path='/cart' element={<Cart cartItem={cartItem}/>}/> */}
        <Route path="*" element={<NotFoundPage />} />
      </Routes>
      <Footer />
    </>
  );
}

export default App;
