import React from 'react';
import Slider from 'react-slick';
import "./slick.scss";
import "./slick-theme.scss";
import { StockSlide } from '../StockSlide/StockSlide';
import { slides } from '../../../assets/data';

export const StocksSlider = () => {
    const settings = {
        autoplay:true,
        dots: true,
        infinite: true,
        speed: 500,
        slidesToShow: 1,
        slidesToScroll: 1
      };
  return (
    <>
    <div className="slide-con" style={{backgroundColor:'#D9D9D9'}}>
      <div className="container">
      <div className="stock-slider" style={{padding:'50px'}}>      
          <Slider {...settings}>
              {slides.map((slide, i)=>(
                <StockSlide key={i} title={slide.title} subTitle={slide.subTitle} desc={slide.desc} stock={slide.stock} stockTitle={slide.stockTitle}/>
              ))}
          </Slider>
      </div> 
      </div> 
    </div> 
          
    </>
  )
}
