import React, { useEffect, useState } from "react";
import { ContentHeader } from "../../common/Content/ContentHeader/ContentHeader";
import { ContentScheme } from "../../common/helpers/contentScheme/ContentScheme";
import { HeadMenu } from "../../common/helpers/HeadMenu/HeadMenu";
import { useParams } from "react-router-dom";
import { Breadcrumbs, Typography, Grid } from "@mui/material";
import goodsService from "../../services/goodsService";
import Constants from "../../common/constants";
import { Button } from "../../common/helpers/buttons/Button";
import "./aboutGood.scss";
import { Link } from "react-router-dom";
import { useDispatch } from "react-redux";
import { addToCart } from "../../store/slices/Cart/cartSlice";
import {StocksSign} from '../../common/helpers/Stocks/StocksSign/StocksSign'

export const AboutGood = () => {
  const { catId, goodId } = useParams();
  const [category, setCategory] = useState("");
  const [good, setGood] = useState("");

  const dispatch = useDispatch();

  useEffect(() => {
    goodsService.getCategories({ catId }).then((response) => {
      setCategory(response[0]);
    });

    goodsService.getGoodById({ goodId }).then((response) => {
      setGood(response[0]);
    });
  }, [catId, goodId]);

  const handleAddToCart = (good) => {
    dispatch(addToCart(good));
    console.log(456465);
  };

  return (
    <>
      <ContentHeader />
      <HeadMenu />
      <ContentScheme>
        <Breadcrumbs
          className="breadcrumbs"
          aria-label="breadcrumb"
          sx={{ color: "rgba(0,0,0,.20)", marginBottom: "20px" }}
        >
          <Link underline="hover" color="inherit" to="/">
            Главная
          </Link>
          <Link underline="hover" color="inherit" to="/catalog">
            Каталог
          </Link>
          <Link underline="hover" color="inherit" to={"/category/" + catId}>
            {category.title}
          </Link>
          <Typography color="text.primary">{good.title}</Typography>
        </Breadcrumbs>
        <Grid className="good-container">
          <Grid className="good-head">
            <Grid className="good-images">
              <img src={Constants.BASE_URL + good.img} alt="" />
              <div className="stock-sign-con">                  
                  { good.discount ? 
                  <div className="stock-sign">
                    <StocksSign>
                        {good.discount}
                      </StocksSign>
                      </div> : null }                                    
                </div>  
            </Grid>
            <Grid className="good-info">
              <div>
                <h3>{good.title}</h3>
                <span>{good.price} тг.</span>
              </div>
              <p className="short-desc">{good.short_desc}</p>
              <Button
                label="Добавить в корзину"
                width="100%"
                hover="true"
                btnFontWeight="true"
                onClick={() => handleAddToCart(good)}
              ></Button>
              <h4>Характеристики</h4>
              <ol>
                {(() => {
                  const options = [];
                  let i = 0;
                  for (const goodSpec in good.specification) {
                    options.push(
                      <li key={i}>
                        <div>{goodSpec}</div>
                        <div></div>
                        <div>{good.specification[goodSpec]}</div>
                      </li>
                    );
                    i++;
                  }
                  return options;
                })()}
              </ol>
            </Grid>
          </Grid>
          <Grid className="good-body">
            <h4>Описание</h4>
            <p>{good.full_desc}</p>
          </Grid>
        </Grid>
      </ContentScheme>
    </>
  );
};
