import React from 'react'
import { Link } from 'react-router-dom'
import './header.scss';
import { useTranslation } from 'react-i18next';
import { Container } from '@mui/system';
import Grid from '@mui/material/Grid';

export const Header = () => {

  const {t, i18n } = useTranslation();

  const changeLanguage = (language) => {
    i18n.changeLanguage(language);
  }
  return (
    <>
    
      <section className='header'>
      <Container>
        <Grid container >
          <Grid item xs={6} md={6} align="left">
                <div className="left row">
                    <i className='fa fa-phone-alt'></i>
                    <label>+7 (7262) 65 32 45</label>
                </div>
          </Grid>
          <Grid item xs={6} md={6} align="right">
                <div className="right row d_flex">
                    <div className="search-input">
                        <i className="fa fa-search"></i>
                        <input type="text" />
                    </div>
                    <div className="sign-in">
                        <i className="fas fa-user-alt"></i>
                        <Link to='/login'>{t("cabinetName")}</Link>
                    </div>                    
                    <select name="lang" id="lang" className='lang' onChange={(e) => changeLanguage(e.target.value)} >
                            <option value="ru">RU</option>
                            <option value="kz">KZ</option>
                    </select>                     
                </div>
          </Grid>
          </Grid>
          </Container>
      </section>
    
    </>
    
  )
}
