import { category, goods } from "../common/assets/data";

const goodsService = {
    getGoods: ({from, to, catId})=>{
        const sortData = goods.filter((good)=>{
            return good.cat_id === catId;
        });

        const data = sortData.slice(from, to);

        return new Promise((resolve, reject) => {
           resolve({
            count: goods.length,
            data: data
           }) 
        });
    },

    getGoodById: ({goodId})=>{
        const findedGood = goods.filter((good)=>{
            return good.id===goodId;
        })
        return new Promise((resolve, reject)=>{
            resolve(
                findedGood
            )
        })
    },

    getCategories: ({catId})=>{

        const categories = category.filter((category)=>{
            return category.id === catId;
        });

        return new Promise((resolve, reject)=>{
            resolve(
                categories
               ) 
        });
    }
}

export default goodsService; 