import React, { useEffect } from "react";
import { Button } from "@mui/material";
import { Typography, Breadcrumbs } from "@mui/material";
import Box from "@mui/material/Box";
import { useSelector } from "react-redux";
import { Grid } from "@mui/material";
import Constants from "../../common/constants";
import { ContentHeader } from "../../common/Content/ContentHeader/ContentHeader";
import { ContentScheme } from "../../common/helpers/contentScheme/ContentScheme";
import { HeadMenu } from "../../common/helpers/HeadMenu/HeadMenu";
import Adverts from "../Adverts";
import { Link } from "react-router-dom";
import { useNavigate } from "react-router-dom";
import ArrowBackIcon from "@mui/icons-material/ArrowBack";
import "./basket.scss";
import DeleteForeverIcon from "@mui/icons-material/DeleteForever";
import AddIcon from "@mui/icons-material/Add";
import RemoveIcon from "@mui/icons-material/Remove";
import { useDispatch } from "react-redux";
import { StocksSign } from '../../common/helpers/Stocks/StocksSign/StocksSign'
import {
  addToCart,
  removeFromCart,
  minusQauntity,
  getTotals,
} from "../../store/slices/Cart/cartSlice";

export const Basket = () => {
  const dispatch = useDispatch();
  const navigate = useNavigate();
  const cart = useSelector((state) => state.cart);

  useEffect(() => {
    dispatch(getTotals());
  }, [cart]);

  const handleAddToCart = (good) => {
    dispatch(addToCart(good));
  };

  const handleRemoveFromCart = (good) => {
    dispatch(removeFromCart(good));
  };

  const handleMinusQuantity = (good) => {
    dispatch(minusQauntity(good));
  };
  return (
    <>
      <ContentHeader />
      <HeadMenu />
      <ContentScheme sidebar={Adverts()}>
        <h1>Корзинка</h1>
        <Breadcrumbs
          className="breadcrumbs"
          aria-label="breadcrumb"
          sx={{ color: "rgba(0,0,0,.20)" }}
        >
          <Link underline="hover" color="inherit" to="/">
            Главная
          </Link>
          <Link underline="hover" color="inherit" to="/catalog">
            Каталог
          </Link>
          <Typography color="text.primary">Корзинка</Typography>
        </Breadcrumbs>
        <Box className="">
          <Grid>
            <div className="cart-items">
              {cart.cartItems.length === 0 ? (
                <div className="noBasket">
                  <Typography
                    sx={{
                      textAlign: "center",
                      marginTop: "20px",
                      fontSize: "20px",
                    }}
                  >
                    Ваша корзина пусто
                  </Typography>
                  <Button
                    sx={{ margin: "20" }}
                    onClick={() => navigate(-1)}
                    startIcon={<ArrowBackIcon />}
                  >
                    Вернуться назад
                  </Button>
                </div>
              ) : (
                <div className="basket">
                  {cart.cartItems?.map((cartItem, i) => (
                    <div className="cart-item">
                      <div className="cart-num">{++i}</div>
                      <div className="cart-info">
                        <div className="img">
                          <img src={Constants.BASE_URL + cartItem.img} alt="" />
                          <div className="stock-sign-con">  
                          { cartItem.discount ? 
                            <div className="stock-sign">
                              <StocksSign>
                                  {cartItem.discount}
                                </StocksSign>
                            </div> : null } 
                            </div>
                        </div>
                        <div className="desc">
                          <h4>{cartItem.title}</h4>
                          <p>{cartItem.short_desc}</p>
                          <h5>Фирма: -</h5>
                        </div>
                        <div className="actions">
                          <p className="price">
                          { cartItem.discount ? 
                            <p className="stock-title">Цена со скидкой {cartItem.discount}%</p> : null } 
                            {cartItem.price * cartItem.cartQuantity} тг.
                            <br />
                            <span>
                            { cartItem.discount ? 
                            cartItem.priceWithoutStock : cartItem.price }  KZT/шт</span>
                          </p>
                          <div className="button-group">
                            <div className="btn-gr">
                              <button
                                onClick={() => handleMinusQuantity(cartItem)}
                              >
                                <RemoveIcon />
                              </button>
                              <input
                                type="number"
                                value={cartItem.cartQuantity}
                              />
                              <span> шт</span>
                              <button onClick={() => handleAddToCart(cartItem)}>
                                <AddIcon />
                              </button>
                            </div>
                            <button
                              className="delete-btn"
                              onClick={() => handleRemoveFromCart(cartItem)}
                            >
                              <DeleteForeverIcon />
                            </button>
                          </div>
                        </div>
                      </div>
                    </div>
                  ))}
                  <div className="cart-info-bottom">
                    <Grid className="left">
                      Общий количество товаров:
                      <span> {cart.cartTotalQuantity}</span>
                    </Grid>
                    <Grid className="right">
                      <p><strong>Сумма:</strong> <span>{cart.cartTotalAmount} КZT</span></p>
                      <p><strong>Cкидка:</strong> <span>-</span></p>
                      <p><strong>Итого:</strong> <strong><span>{cart.cartTotalAmount} КZT</span></strong></p>
                    </Grid>
                  </div>
                  <div className="btn-order">
                    <button
                      style={{ margin: "20" }}
                      onClick={() => navigate(-1)}
                    >
                      <ArrowBackIcon />
                      Вернуться назад
                    </button>
                    <button>Оформить заказ</button>
                  </div>
                </div>
              )}
            </div>
          </Grid>
        </Box>
      </ContentScheme>
    </>
  );
};
