import React from 'react'
import { Benefits } from '../../common/helpers/benefits/Benefits';
import { Heading } from '../../common/helpers/heading/Heading';
import { benefits, aboutCompany } from '../../common/assets/data';
import { Button } from '../../common/helpers/buttons/Button';
import { Link } from 'react-router-dom'
import './about.scss'

export const About = () => {
  return (
    <>
          <div className="container about-com c_flex">
              <div className="heading-title f_flex">
                <Heading title={aboutCompany.title} width='400px' fontSize='26px'/>
              </div>
              <div className="about-text f_flex">
                <p>{aboutCompany.desc}</p>
              </div>
              <div className="about-benefits">
                {
                  benefits.map((benefit, i) =>(
                    <Benefits key={i} title={benefit.title} desc={benefit.desc} width="200px"/>
                  ))
                }                                 
              </div>
              <div className="about-btn">
                <Link to={aboutCompany.link}>
                  <Button label={aboutCompany.linkTitle} width="250px" hover='black'/> 
                </Link>                  
              </div>
          </div>
    </>
  )
}
